
$maincolor : #ff0000;

.red {
    color: $maincolor;
}

.my-alert{
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert2{
    @extend .my-alert; //my-alert 속성을 그대로 가져온다
    background: #ffe591;
}

@mixin fnAlertCss() {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert3{
    @include fnAlertCss();
    background: rgb(128, 208, 223);    
}


//nesting 문법
div.container {
    h4 {
        color:orange;
    }
    p {
        color: green;
    }
}


.my-alert3 p{
    margin-bottom: 0;
}

.my-alert p{
    margin-bottom: 0;
}



